    padding: 16px 21px 17px 21px;
    box-sizing: border-box;
    font-family: "Inter UI";
    transition: 0.5s;
    text-decoration: none;
    position: relative;
    border-bottom: 1px solid #000000; }
    .taxesCard .illustration {
      top: 0px;
