@media (max-width: 767px) {
  .taxesWrapper {
    width: 320px;
    margin: 0 auto;
    padding-top: 84px;
    padding-bottom: 100px;
    font-family: "Inter UI"; }
    .taxesWrapper .taxesWrapperHeader {
      position: relative;
      padding-top: 42px;
      padding-bottom: 15px;
      width: 100%;
      height: 49px; }
      .taxesWrapper .taxesWrapperHeader h1 {
        position: absolute;
        text-align: center;
        width: 100%;
        top: 30px;
        font-weight: 600;
        font-size: 35px;
        line-height: 54px;
        text-align: center;
        letter-spacing: -0.01em;
        color: #040404;
        opacity: 0.9;
        z-index: 0; }
      .taxesWrapper .taxesWrapperHeader a {
        display: flex;
        position: absolute;
        z-index: 10;
        width: 36px;
        height: 36px;
        z-index: 1;
        border-radius: 44px;
        box-shadow: 0px 5px 22px rgba(0, 0, 0, 0.25); }
        .taxesWrapper .taxesWrapperHeader a img {
          width: 36px;
          height: 36px;
          position: absolute;
          cursor: pointer; }
    .taxesWrapper .taxesCardsWrapper {
      display: flex;
      flex-wrap: wrap;
      border-top: 1px solid black; } }

