@media (max-width: 767px) {
  .servicesWrapper {
    width: 100vw;
    font-family: "Inter UI";
    height: 560px;
    position: relative;
    background-color: #8A6E59; }
    .servicesWrapper .anchor {
      position: absolute;
      top: -84px; }
    .servicesWrapper .services {
      width: 300px;
      margin: 0 auto; }
      .servicesWrapper .services h1 {
        padding-top: 24px;
        font-weight: 600;
        font-size: 27px;
        line-height: 131.56%;
        color: white;
        text-align: center;
        letter-spacing: -0.02em; }
      .servicesWrapper .services .servicesItems {
        padding-top: 13px;
        width: 100%;
        margin: 0 auto;
        display: flex;
        flex-flow: column;
        justify-content: space-between; }
        .servicesWrapper .services .servicesItems .servicesItem {
          cursor: pointer;
          width: 100%;
          display: flex;
          flex-flow: column;
          border-radius: 13px 13px 0px 0px;
          margin-bottom: 25px; }
          .servicesWrapper .services .servicesItems .servicesItem img {
            border-radius: 13px 13px 0px 0px;
            object-fit: cover;
            height: 80px; }
          .servicesWrapper .services .servicesItems .servicesItem h2 {
            padding: 8px 0 7px;
            text-align: center;
            font-weight: 700;
            font-size: 20px;
            line-height: 131.56%;
            color: #4F261C;
            width: 100%;
            background-color: white; }
      .servicesWrapper .services .servicesCall {
        display: none;
        width: 296px;
        height: 68px;
        margin: 0 auto;
        justify-content: center;
        align-items: center;
        background: #4F261C;
        box-shadow: 4px 4px 44px rgba(98, 48, 36, 0.44);
        border-radius: 20px;
        text-decoration: none;
        transition: 0.2s ease-in-out; }
        .servicesWrapper .services .servicesCall span {
          text-decoration: none;
          padding-left: 6px;
          display: flex;
          align-self: center;
          font-weight: 600;
          font-size: 23px;
          line-height: 28px;
          color: white; }
        .servicesWrapper .services .servicesCall:hover {
          transition: 0.2s ease-in-out;
          box-shadow: 4px 4px 64px #4F261C; } }

